<template>
	<view class="page">
		<view class="input-t" @tap.stop="chooseType">
			<text>类型</text>
			<text class="input-quick">{{ mtype || '请选择类型' }} <text class="iconfont iconxia"></text></text>
		</view>
		
		<view class="input-t">
			<text>申请人姓名</text>
			<input class="input-quick" style="text-align: right;" placeholder="请输入姓名" v-model="form.userName" :disabled="true" />
		</view>
		
		<view class="input-t">
			<text>手机号码</text>
			<input type="number" style="text-align: right;" v-model="form.phoneNumber" placeholder="请输入手机号码" maxlength="11"/>
		</view>

		<button
			class="confirm-btn"
			:class="'bg-' + themeColor.name"
			:disabled="btnLoading"
			:loading="btnLoading"
			@tap="send"
		>
			提交申请
		</button>

	</view>
</template>

<script>
	import requests from '@/utils/requests.js'
export default {
	data() {
		return {
			type:'',
			mtype:'',
			mtypeValue:'', // 选中下标
			sendDate: {
				remind:'0',
				content: ''
			},
			form:{
				appleType: '',
				phoneNumber: uni.getStorageSync('userInfo').phonenumber,
				classId: uni.getStorageSync('userInfo').classId,
				gradeId: uni.getStorageSync('userInfo').gradeId,
				userName: uni.getStorageSync('userInfo').userName,
				userId: uni.getStorageSync('userInfo').userId,
			},
			worksType:[],
			worksTypeList:[],
			btnLoading: false
		};
	},
	onLoad() {
		this.getDicts()
	},
	onShow() {
		uni.setNavigationBarColor({
		    frontColor: '#ffffff',
		    backgroundColor: this.themeColor.color,
		    animation: {
		        duration: 400,
		        timingFunc: 'easeIn'
		    }
		})
	},
	
	methods: {
		
		getDicts(){
			let that = this
			that.worksType = []
			requests.getDicts('nucleic_acid_testing_vaccination_type').then(res => {
				console.log(res)
				if(res && res.code === 200){
					res.data.forEach((item) => {
						let obj = Object.assign({
							id: item.dictValue,
							name: item.dictLabel
						})
						that.worksTypeList.push(obj)
					})
					that.worksTypeList.forEach((item) =>{
						that.worksType.push(item.name)
					})
				}
			})
		},
		
		// 监听反馈类型事件
		handleRemindTypeChange(e) {
			this.sendDate.remind = e.detail.value;
		},
		chooseType() {
			uni.showActionSheet({
				itemList: this.worksType,
				success: res => {
					console.log('res===>',res)
					this.mtype = this.worksType[res.tapIndex];
					this.mtypeValue = res.tapIndex;
					this.worksTypeList.forEach((item,index) => {
						if(res.tapIndex === index){
							console.log(item)
							this.form.appleType = item.id
						}
					})
				},
				fail: res => {
					console.log(res);
				}
			});
		},
		// 发送数据
		send() {
			console.log('form===>', this.form)
			if (this.form.appleType === '' || this.form.appleType === null) {
				uni.showToast({
					title: '请选择类型',
					icon: 'none'
				})
				return
			}
			if (this.form.phoneNumber === '' || this.form.phoneNumber === null) {
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none'
				})
				return
			}
			
			var reg = /^1[3456789]\d{9}$/;
			if (!reg.test(this.form.phoneNumber)) {
				uni.showToast({
					title: '请输入正确的手机号码',
					icon: 'none'
				})
				return
			}
			

			requests.addVaccination(this.form).then(res => {
				if(res && res.code === 200){
					uni.showToast({
						title: res.msg,
						icon: 'success',
					})
				}
			})
		}
	}
};
</script>

<style lang="scss">
page {
	background-color: $page-color-base;
}
/*表单标题*/
.input-t {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: $spacing-base;
	margin-top: $spacing-base;
	font-size: $font-base;

}
.input-quick {
	position: relative;
	padding-right: 40upx;
	.iconfont {
		font-size: $font-sm;
	}
}

.input-body {
	background: $color-white;
	padding: $spacing-sm $spacing-base;
	.gender-item {
		margin-right: 20upx;

		.gender-item-text {
			padding-left: 10upx;
		}

	}
}

.input-textare {
	height: 200upx;
	font-size: 34upx;
	line-height: 50upx;
	width: 100%;
	box-sizing: border-box;
	padding: 20upx 30upx 0;
}

</style>
